<template>
  <div id="app">
    <div class="today">
      <p class="weather-title">成都{{weatherObj.day}}天气情况</p>
      <div class="weather">
        <img :src="weatherObj.day_weather_pic" alt="">
        <ul>
          <li>气温：{{weatherObj.day_air_temperature}}°C</li>
          <li>天气情况：{{weatherObj.day_weather}}</li>
          <li>日出日落时间：{{weatherObj.sun_begin_end}}</li>
          <li>降水概率：{{weatherObj.jiangshui}}</li>
        </ul>
      </div>
    </div>
    <div class="axita">
      <p class="weather-title">成都{{weatherObj2.day}}天气情况</p>
      <div class="weather">
        <img :src="weatherObj2.day_weather_pic" alt="">
        <ul>
          <li>气温：{{weatherObj2.day_air_temperature}}°C</li>
          <li>天气情况：{{weatherObj2.day_weather}}</li>
          <li>日出日落时间：{{weatherObj2.sun_begin_end}}</li>
          <li>降水概率：{{weatherObj2.jiangshui}}</li>
        </ul>
      </div>
    </div>
    <div class="today">
      <p class="weather-title">成都{{weatherObj3.day}}天气情况</p>
      <div class="weather">
        <img :src="weatherObj3.day_weather_pic" alt="">
        <ul>
          <li>气温：{{weatherObj3.day_air_temperature}}°C</li>
          <li>天气情况：{{weatherObj3.day_weather}}</li>
          <li>日出日落时间：{{weatherObj3.sun_begin_end}}</li>
          <li>降水概率：{{weatherObj3.jiangshui}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        weatherObj: {},
        weatherObj2: {},
        weatherObj3: {}
      }
    },

    filters: {
      getDate: (val) => {
        let now = val;
        let year = now.substring(0, 4);
        let month = now.substring(4, 6);
        let day = now.substring(6, 8);
        let time = year + '年' + month + '月' + day + '日';
        return time;
      }
    },
    methods: {
      GetNowDateTime: (val) => {
        let nowTime = val;
        let NowDateTime = nowTime.getFullYear() + "年" +
            (nowTime.getMonth() + 1 ) + "月" +
            nowTime.getDate() + "日" +
            " ";
        return NowDateTime;
      }
    },
    mounted: function () {
      let url = 'https://route.showapi.com/9-2';
      let showapi_appid = 'showapi_appid=32114';
      let showapi_sign = 'showapi_sign=6b8a7f08662149a98783bd2d401e984a';
      let city = 'area=成都'
      this.$http.get(url + '?' + showapi_appid + '&' + showapi_sign + '&' + city)
          .then(res => {
            this.weatherObj = res.body.showapi_res_body.f1;
            this.weatherObj2 = res.body.showapi_res_body.f2;
            this.weatherObj3 = res.body.showapi_res_body.f3;
          });
    },
    components: {}
  }
</script>

<style>
  * {
    margin: 0;
    padding: 0;
    font: 12px/24px "微软雅黑";
  }

  ul, li {
    list-style: none;
  }

  #app {
    padding: 20px 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .weather-title {
    text-align: center;
    font-size: 18px;
    color: #1c8de0;
  }

  .weather {
    padding-top: 40px;
    display: flex;
  }

  .weather img {
    padding-right: 20px;
  }

</style>



